.wrapper {
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    .titleText {
        font-size: 50px;
        color: #FFFFFF;
        font-family: 'Playball';
        margin-bottom: 20px;
    }

    main {
        position: relative;
        display: flex;
    }

    .refresh {
        font-size: 64px;
        color: #fff;
        cursor: pointer;
    }
}

.modalBody {
    display: flex;
    justify-content: center;

    h1 {
        font-size: 22px;
        font-family: 'Roboto', sans-serif;
        color: #eee;
        margin-bottom: 10px;
    }
}

.modalFooter {
    display: flex;
    justify-content: center;
}